* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.nav {
  z-index: 100;
  width: 100%;
  background-color: #fff;
  position: fixed;
  top: 0;
  left: 0;
}

#search-box {
  font: inherit;
  max-width: 300px;
  border: 2px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  margin-top: 20px;
  margin-left: 40px;
  padding: 8px;
}

#search-box:hover, #search-box:focus {
  border: 1px solid #2196f3;
}

.country-list {
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
}

li {
  margin: 5px;
  list-style: none;
}

.div__all-country {
  padding-top: 100px;
}

.title__all-countries-invisible {
  opacity: 0;
  justify-content: center;
  display: flex;
}

.title__all-countries-visible {
  opacity: 1;
  justify-content: center;
  display: flex;
}

.projects-list {
  flex-wrap: wrap;
  margin: -15px;
  display: flex;
}

.projects-list__img {
  display: block;
}

.projects-list__item {
  width: 200px;
  margin: 15px;
  transition-duration: .25s;
}

.projects-list__item:hover, .projects-list__item:focus {
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .25));
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.projects-list__box {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.projects-list__description {
  width: 100%;
  height: 100%;
  letter-spacing: .03em;
  color: #fff;
  opacity: 0;
  padding: .7rem;
  font-size: 1.1rem;
  font-style: normal;
  font-weight: 400;
  line-height: 1.2rem;
  transition-duration: .45s;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(100%);
}

.projects-list__item:hover .projects-list__description {
  opacity: 1;
  background-color: rgba(33, 150, 243, .9);
  transform: translateY(0);
}

.projects-list__link {
  height: 100%;
  flex-direction: column;
